.side{
  width: 50vw;
  height: 100vh;

}
.sideContainer{
  display: flex;
  overflow: hidden;
}
.left{
  position: relative;
}
.right{

}
/************************* LEFT SIDE ***********************************/
//toggle navbar
.hOnNavBar{

  height: 5rem;
  position: absolute;top: 0;right: 0;
  width: 0;z-index: 3;
  overflow: hidden;
  transition: width 0.2s ease-in;
}
.hambOn{
  width: 100% !important;
}
//side Text
.textWhite{
  position: relative;
  padding: 2rem 3rem;
  background-color: rgba(255, 255, 255, 0.014);

  backdrop-filter: blur( 3px );


  border-bottom-right-radius: 8px;
  border-top-right-radius: 8px;


  h1{font-size: $fsTitle;font-family: $ffFutura;}
  h2{font-size: $fsSsTitle;font-family: $ffFutura;}
  p{font-size: $fsText;margin: 1.2rem 0;font-family: $ffGotham;}

  width: 90%;

  margin: 3.5rem 0;
}
//hNavBar

.hNavBar{
  justify-content: space-between;
  margin-top: 20px;
}

//Btn programme
.programmeSec{
  width: 100%;
  align-items: center;
  justify-content: space-between;
  transform: translate(2rem, 1rem);
  margin-top: 10rem;
}
//le reste dans components

//bulles bleues
#bHG{transform: translate(-50px ,-50px);}
#bHD{right: 15%;transform: translateY(-10px);}
#bM{right: 2%;top: 50%;}
#bBG{left: 5%;bottom: 6%;}
#bBD{right: 0;bottom: 0;transform: translateY(80%);}

/************************* Right SIDE ***********************************/

//bandeaux des 4 tables
.bande{
  width: 100%;
  background-position: center ;
  background-repeat: repeat;
  background-size: cover;
  position: relative;
  cursor: pointer;


}
.buttonProg{
  justify-content: space-between;
  position: absolute;
  bottom: 20%;right: 2%;
}
